import React from "react";
import {
  Grid,
  Typography,
  Table,
  TableHead,
  TableBody,
  TableCell,
  TableRow,
  Icon,
  Button
} from "@material-ui/core";
import request from "../helpers/request";
import Loader from "../components/Loader";
import Navbar from "../components/Navbar";

const RenderItems = ({ items = [], renderItem }) => items.map(renderItem);
const options = [
  {
    view: "Proyectos",
    label: "Proyectos"
  },
  {
    view: "EditarProyecto",
    label: "Edici√≥n"
  }
];
class DetalleProyecto extends React.Component {
  params = this.props.navigation.params();
  state = {
    cuentas: [],
    others: 0.0,
    showLoader: true
  };

  componentDidMount() {
    this.GetCategories();
  }

  async GetCategories() {
    const state = {
      cuentas: [],
      others: 1,
      showLoader: false
    };

    let response = await request.get(
      `/cuentas?projectId=${this.params.projectId}&projectCode=${this.params.projectCode}`
    );
    if (response.error) {
      alert("Favor de revisar error en la consola");
      console.log(response.message);
      this.setState({ showLoader: false });
      return;
    }    
    state.cuentas = response.data;
    ///accounts-others
    response = await request.get(
      `/otras-cuentas?segmento=${this.params.projectCode}`
    );

    if (response.error) {
      alert("Favor de revisar error en la consola");
      console.log(response.message);
      this.setState({ showLoader: false });
      return;
    }
    state.others = response.data;
    this.setState(state);
  }

  render() {
    const { showLoader, cuentas } = this.state;
    return (
      <>
        <Loader show={showLoader} />
        <Navbar
          options={options}
          onClick={view => this.props.navigation.go(view)}
        />
        <Grid container>
          <Grid item sm={12}>
            <Typography>
              Proyecto {this.params.projectCode} - {this.params.projectName}
            </Typography>
          </Grid>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell>Codigo</TableCell>
                <TableCell>Nombre</TableCell>
                <TableCell>Presupuesto</TableCell>
                <TableCell>Ordenes de Compra</TableCell>
                <TableCell>Pagado</TableCell>
                <TableCell>Resta</TableCell>
                <TableCell>Acciones</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              <RenderItems
                items={cuentas}
                renderItem={item => (
                  <TableRow key={item.id}>
                    <TableCell>{item.code}</TableCell>
                    <TableCell>{item.name}</TableCell>
                    <TableCell>
                      $
                      {item.presupuesto.toLocaleString("es-Mx", {
                        maximumFractionDigits: 2
                      })}
                    </TableCell>
                    <TableCell>
                      $
                      {item.ordenesCompra.toLocaleString("es-Mx", {
                        maximumFractionDigits: 2
                      })}
                    </TableCell>
                    <TableCell>
                      $
                      {item.pagado.toLocaleString("es-Mx", {
                        maximumFractionDigits: 2
                      })}
                    </TableCell>
                    <TableCell>
                      $
                      {(item.presupuesto - item.ordenesCompra).toLocaleString(
                        "es-Mx",
                        {
                          maximumFractionDigits: 2
                        }
                      )}
                    </TableCell>
                    <TableCell>
                      <Button
                        onClick={() =>
                          this.props.navigation.go("Subcuentas", {
                            projectId: this.params.projectId,
                            projectCode: this.params.projectCode,
                            projectName: this.params.projectName,
                            accountCode: item.code,
                            accountId: item.id
                          })
                        }
                      >
                        <Icon>format_align_justify</Icon>
                      </Button>
                    </TableCell>
                  </TableRow>
                )}
              />
              <TableRow>
                <TableCell>Otros</TableCell>
                <TableCell>OTROS</TableCell>
                <TableCell colSpan={3}>
                  {this.state.others.toLocaleString("es-Mx", {
                    maximumFractionDigits: 2
                  })}
                </TableCell>
                <TableCell>
                  <Button
                    onClick={() =>
                      this.props.navigation.go("DetalleCuentasOtros", {
                        others: true,
                        projectId: this.params.projectId,
                        projectCode: this.params.projectCode,
                        projectName: this.params.projectName
                      })
                    }
                  >
                    <Icon>format_align_justify</Icon>
                  </Button>
                </TableCell>
              </TableRow>
            </TableBody>
          </Table>
        </Grid>
      </>
    );
  }
}

export default DetalleProyecto;
